.examplesPage html,
.examplesPage body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  color: #626466;
  font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.625;
  height: 100%;
}
@media only screen and (max-width: 680px) {
  .examplesPage body {
    font-size: 14px;
  }
}
.examplesPage h1,
.examplesPage h2,
.examplesPage h3,
.examplesPage h4,
.examplesPage h5,
.examplesPage h6 {
  color: #212325;
}
.examplesPage a {
  color: #4183C4;
  text-decoration: none;
  cursor: pointer;
}
.examplesPage pre,
.examplesPage code {
  font-family: 'Fira Mono', Menlo, monospace;
  background: #F9F8F7;
  color: #484A4C;
  font-size: 0.9375em;
  letter-spacing: -0.015em;
}
.examplesPage code {
  margin: -0.05rem -0.15em;
  padding: 0.05rem 0.35em;
}
.examplesPage blockquote {
  margin: 1rem 0;
  padding: 0 1rem;
  color: #727476;
  border-left: solid 3px #DCDAD9;
}
.examplesPage blockquote > :first-child {
  margin-top: 0;
}
.examplesPage blockquote > :last-child {
  margin-bottom: 0;
}
.examplesPage .codeBlock {
  -webkit-overflow-scrolling: touch;
  background: #FCFBFA;
  border-left: solid 3px #ECEAE9;
  box-sizing: border-box;
  display: block;
  font-size: 0.875em;
  margin: 0.5rem 0;
  overflow-y: scroll;
  padding: 0.5rem 8px 0.5rem 12px;
  white-space: pre;
}
.examplesPage .t.blockParams {
  padding-left: 2ch;
}
.examplesPage .token.punctuation,
.examplesPage .token.ignore,
.examplesPage .t.interfaceDef,
.examplesPage .t.member,
.examplesPage .t.callSig {
  color: #808890;
}
.examplesPage .token.function,
.examplesPage .token.class-name,
.examplesPage .token.qualifier,
.examplesPage .t.fnQualifier,
.examplesPage .t.fnName {
  color: #32308E;
}
.examplesPage .token.primitive,
.examplesPage .t.primitive {
  color: #922;
}
.examplesPage .token.number,
.examplesPage .t.typeParam {
  color: #905;
}
.examplesPage .t.typeQualifier,
.examplesPage .t.typeName {
  color: #013679;
}
.examplesPage .t.param {
  color: #945277;
}
.examplesPage .t.memberName {
  color: teal;
}
.examplesPage .token.block-keyword,
.examplesPage .token.keyword,
.examplesPage .t.keyword {
  color: #A51;
}
.examplesPage .token.string,
.examplesPage .token.regex {
  color: #df5050;
}
.examplesPage .token.operator {
  color: #a67f59;
}
.examplesPage .token.comment {
  color: #998;
  font-style: italic;
}
.examplesPage .active-row .public_fixedDataTableCell_main {
  background: yellow;
}
.examplesPage .highlight-row .public_fixedDataTableCell_main {
  background: lightyellow;
}
.examplesPage input {
  padding: 10px 8px;
  margin-bottom: 8px;
}
.examplesPage .pageBody {
  padding: 0 36px;
  position: relative;
}
.examplesPage .contents {
  margin: 0 auto;
  padding: 50px 0 0;
}
.examplesPage .sidebarItemText,
.examplesPage .arrowBullet {
  display: inline-block;
  vertical-align: middle;
}
.examplesPage .arrowBullet {
  visibility: hidden;
  width: 6px;
  height: 9px;
  background: url(images/arrowBullet-a7498792e489de18e0ea078a2f7f9984.png) no-repeat scroll;
  margin-left: 9px;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .examplesPage .arrowBullet {
    background-image: url(images/arrowBullet-2x-f7897a085ed30ed69fac8e5259f75421.png);
    background-size: contain;
  }
}
.examplesPage .disclaimer {
  margin: 60px 20px 0 260px;
  border: solid 1px #eecccc;
  background: #fefafa;
  padding: 1em;
  text-align: center;
  font-size: 0.8em;
}
@media only screen and (max-width: 680px) {
  .examplesPage .disclaimer {
    margin: 60px 0 0;
  }
}
@media only screen and (max-width: 680px) {
  .examplesPage .miniHeader {
    position: relative;
  }
}
.examplesPage .miniHeaderContents {
  margin: 0 auto;
  padding: 12px 36px;
  position: relative;
  text-align: right;
}
@media only screen and (max-width: 680px) {
  .examplesPage .miniHeaderContents {
    text-align: left;
  }
}
.examplesPage .miniLogo {
  float: left;
  left: -140px;
  top: 12px;
}
.examplesPage .miniLogo > .svg {
  height: 24px;
}
.examplesPage .miniHeaderContents a {
  color: #fff;
  margin-right: 1em;
  text-decoration: none;
  text-shadow: 0 1px rgba(0, 0, 0, 0.15);
}
.examplesPage .miniHeaderContents a:last-child {
  margin-right: 0;
}
.examplesPage .sideBar {
  cursor: default;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  font-size: 0.825em;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  z-index: 0;
}
.examplesPage .sideBar .scrollContent {
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
  padding: 66px 0 100px;
  width: 160px;
}
@media only screen and (max-width: 680px) {
  .examplesPage .sideBar {
    position: relative;
    width: 100%;
  }
  .examplesPage .sideBar .scrollContent {
    padding: 0;
    width: 100%;
  }
  .examplesPage .pageBody {
    padding: 0;
  }
  .examplesPage .contents {
    padding: 0;
  }
}
.examplesPage .sideBar h2 {
  font-size: 1em;
  margin: 1em 0;
  position: relative;
}
.examplesPage .sideBar h2 a {
  font-weight: normal;
}
.examplesPage .sideBar .curSideItem {
  font-weight: bold;
}
.examplesPage .sideBar .curSideItem .arrowBullet {
  visibility: visible;
}
.examplesPage .sideBar .members {
  margin: 0 0 2.5em 1em;
}
.examplesPage .sideBar .groupTitle {
  color: #626466;
  font-size: 1em;
  margin: 1em 0 0;
}
.examplesPage .exampleHeader {
  margin-bottom: 18px;
}
.examplesPage .exampleTitle {
  margin-bottom: 0;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.examplesPage .exampleDescription {
  clear: both;
  overflow: auto;
  max-height: 57px;
  margin: 0;
}
.examplesPage .exampleCode {
  float: right;
}
.examplesPage .exampleTitleName {
  color: #999;
}
.examplesPage .exampleControls {
  float: right;
  height: 30px;
}
.examplesPage .exampleContents {
  font-family: Helvetica, Arial, 'lucida grande', tahoma, verdana, arial, sans-serif;
  font-size: 12px;
  margin-left: 160px;
}
@media only screen and (max-width: 680px) {
  .examplesPage .exampleContents {
    margin-left: 0;
  }
}
.examplesPage .t a {
  -webkit-transition: background-color 0.15s;
  -moz-transition: background-color 0.15s;
  -ms-transition: background-color 0.15s;
  -o-transition: background-color 0.15s;
  background-color: rgba(0, 0, 0, 0.01);
  border-radius: 4px;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.08);
  margin: -2px -4px;
  padding: 2px 4px;
}
.examplesPage .t a:hover {
  background-color: rgba(112, 170, 220, 0.2);
}
.examplesPage .typeHeader {
  color: #555;
  font-size: 1.5em;
  font-weight: normal;
  margin: 1rem 0;
  font-weight: bold;
}
.examplesPage .interfaceMember {
  margin: 1.3rem 0;
}
.examplesPage .infoHeader {
  color: #555;
  font-size: 10px;
  letter-spacing: 0.25ch;
  line-height: 16px;
  margin: 1rem 0 0.125rem;
  text-transform: uppercase;
}
.examplesPage .synopsis {
  margin: -0.5em 0 1em;
}
.examplesPage .discussion p:first-child {
  margin-top: 0.5em;
}
.examplesPage .memberSignature {
  border-left-color: #9CDAE9;
  background: #F8F9FA;
}
.examplesPage .t.over {
  border-bottom: solid 2px rgba(0, 0, 0, 0.05);
  padding-bottom: 3px;
}
.examplesPage .memberLabel {
  cursor: default;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  font-size: 1em;
  margin: 0;
}
@media only screen and (max-width: 680px) {
  .examplesPage .memberLabel {
    cursor: pointer;
  }
}
.examplesPage .detail {
  box-sizing: border-box;
  margin-bottom: 2.6rem;
  overflow: hidden;
}
.examplesPage .groupTitle {
  color: #9A9C9E;
  font-size: 1.5em;
  font-weight: 300;
  margin: 3rem 0 2rem;
}
@media only screen and (max-width: 680px) {
  .examplesPage .groupTitle {
    margin: 2em 0 1em;
  }
}
.examplesPage .doc {
  margin: 2em 0 3em;
}
.examplesPage p:last-child {
  margin-bottom: 0;
}
.exampleImage {
  background: #efefef;
  background-size: 100%;
  display: inline-block;
  height: 50px;
  width: 50px;
}
.autoScrollContainer .autoScrollControls {
  display: flex;
  justify-content: space-around;
  align-items: baseline;
}
.autoScrollContainer .autoScrollCell {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
html[dir="rtl"] .exampleContents {
  margin-right: 160px;
}
