.docsPage html,
.docsPage body {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  color: #626466;
  font-family: 'Fira Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 1.625;
  height: 100%;
}
@media only screen and (max-width: 680px) {
  .docsPage body {
    font-size: 14px;
  }
}
.docsPage h1,
.docsPage h2,
.docsPage h3,
.docsPage h4,
.docsPage h5,
.docsPage h6 {
  color: #212325;
}
.docsPage a {
  color: #4183C4;
  text-decoration: none;
  cursor: pointer;
}
.docsPage pre,
.docsPage code {
  font-family: 'Fira Mono', Menlo, monospace;
  background: #F9F8F7;
  color: #484A4C;
  font-size: 0.9375em;
  letter-spacing: -0.015em;
}
.docsPage code {
  margin: -0.05rem -0.15em;
  padding: 0.05rem 0.35em;
}
.docsPage blockquote {
  margin: 1rem 0;
  padding: 0 1rem;
  color: #727476;
  border-left: solid 3px #DCDAD9;
}
.docsPage blockquote > :first-child {
  margin-top: 0;
}
.docsPage blockquote > :last-child {
  margin-bottom: 0;
}
.docsPage .codeBlock {
  -webkit-overflow-scrolling: touch;
  background: #FCFBFA;
  border-left: solid 3px #ECEAE9;
  box-sizing: border-box;
  display: block;
  font-size: 0.875em;
  margin: 0.5rem 0;
  overflow-y: scroll;
  padding: 0.5rem 8px 0.5rem 12px;
  white-space: pre;
}
.docsPage .t.blockParams {
  padding-left: 2ch;
}
.docsPage .token.punctuation,
.docsPage .token.ignore,
.docsPage .t.interfaceDef,
.docsPage .t.member,
.docsPage .t.callSig {
  color: #808890;
}
.docsPage .token.function,
.docsPage .token.class-name,
.docsPage .token.qualifier,
.docsPage .t.fnQualifier,
.docsPage .t.fnName {
  color: #32308E;
}
.docsPage .token.primitive,
.docsPage .t.primitive {
  color: #922;
}
.docsPage .token.number,
.docsPage .t.typeParam {
  color: #905;
}
.docsPage .t.typeQualifier,
.docsPage .t.typeName {
  color: #013679;
}
.docsPage .t.param {
  color: #945277;
}
.docsPage .t.memberName {
  color: teal;
}
.docsPage .token.block-keyword,
.docsPage .token.keyword,
.docsPage .t.keyword {
  color: #A51;
}
.docsPage .token.string,
.docsPage .token.regex {
  color: #df5050;
}
.docsPage .token.operator {
  color: #a67f59;
}
.docsPage .token.comment {
  color: #998;
  font-style: italic;
}
.docsPage .active-row .public_fixedDataTableCell_main {
  background: yellow;
}
.docsPage .highlight-row .public_fixedDataTableCell_main {
  background: lightyellow;
}
.docsPage .pageBody {
  padding: 0 36px;
  position: relative;
}
.docsPage .contents {
  margin: 0 auto;
  max-width: 880px;
  padding: 64px 0;
}
.docsPage .sidebarItemText,
.docsPage .arrowBullet {
  display: inline-block;
  vertical-align: middle;
}
.docsPage .arrowBullet {
  visibility: hidden;
  width: 6px;
  height: 9px;
  background: url(images/arrowBullet-a7498792e489de18e0ea078a2f7f9984.png) no-repeat scroll;
  margin-left: 9px;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .docsPage .arrowBullet {
    background-image: url(images/arrowBullet-2x-f7897a085ed30ed69fac8e5259f75421.png);
    background-size: contain;
  }
}
.docsPage .sideBar .curSideItem .arrowBullet {
  visibility: visible;
}
.docsPage .disclaimer {
  margin: 60px 20px 0 260px;
  border: solid 1px #eecccc;
  background: #fefafa;
  padding: 1em;
  text-align: center;
  font-size: 0.8em;
}
@media only screen and (max-width: 680px) {
  .docsPage .disclaimer {
    margin: 60px 0 0;
  }
}
@media only screen and (max-width: 680px) {
  .docsPage .miniHeader {
    position: relative;
  }
}
.docsPage .miniHeaderContents {
  margin: 0 auto;
  max-width: 880px;
  padding: 12px 36px;
  position: relative;
  text-align: right;
}
@media only screen and (max-width: 680px) {
  .docsPage .miniHeaderContents {
    text-align: left;
  }
}
.docsPage .miniLogo {
  float: left;
  left: -140px;
  top: 12px;
  margin-top: -4px;
}
.docsPage .miniLogo > .svg {
  height: 24px;
}
.docsPage .miniHeaderContents a {
  color: #fff;
  margin-right: 1em;
  text-decoration: none;
  text-shadow: 0 1px rgba(0, 0, 0, 0.15);
}
.docsPage .miniHeaderContents a:last-child {
  margin-right: 0;
}
.docsPage .sideBar {
  cursor: default;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  font-size: 0.825em;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  width: 220px;
  z-index: 0;
}
.docsPage .sideBar .scrollContent {
  box-sizing: border-box;
  height: 100%;
  overflow: auto;
  padding: 66px 0 100px;
  width: 240px;
}
@media only screen and (max-width: 680px) {
  .docsPage .sideBar {
    position: relative;
    width: 100%;
  }
  .docsPage .sideBar .scrollContent {
    padding: 0;
    width: 100%;
  }
  .docsPage .pageBody {
    padding: 0;
  }
  .docsPage .contents {
    padding: 0;
  }
}
.docsPage .sideBar h2 {
  font-size: 1em;
  margin: 1em 0;
  position: relative;
}
.docsPage .sideBar h2 a {
  font-weight: normal;
}
.docsPage .sideBar .members {
  margin: 0 0 2.5em 1em;
}
.docsPage .sideBar .groupTitle {
  color: #626466;
  font-size: 1em;
  margin: 1em 0 0;
}
.docsPage .docContents {
  margin-left: 240px;
}
@media only screen and (max-width: 680px) {
  .docsPage .docContents {
    margin-left: 0;
  }
}
.docsPage .t a {
  -webkit-transition: background-color 0.15s;
  -moz-transition: background-color 0.15s;
  -ms-transition: background-color 0.15s;
  -o-transition: background-color 0.15s;
  background-color: rgba(0, 0, 0, 0.01);
  border-radius: 4px;
  box-shadow: inset 0 0 1px rgba(0, 0, 0, 0.08);
  margin: -2px -4px;
  padding: 2px 4px;
}
.docsPage .t a:hover {
  background-color: rgba(112, 170, 220, 0.2);
}
.docsPage .typeHeader {
  color: #555;
  font-size: 1.5em;
  font-weight: normal;
  margin: 1rem 0;
  font-weight: bold;
}
.docsPage .interfaceMember {
  margin: 1.3rem 0;
}
.docsPage .infoHeader {
  color: #555;
  font-size: 10px;
  letter-spacing: 0.25ch;
  line-height: 16px;
  margin: 1rem 0 0.125rem;
  text-transform: uppercase;
}
.docsPage .synopsis {
  margin: -0.5em 0 1em;
}
.docsPage .discussion p:first-child {
  margin-top: 0.5em;
}
.docsPage .memberSignature {
  border-left-color: #9CDAE9;
  background: #F8F9FA;
}
.docsPage .t.over {
  border-bottom: solid 2px rgba(0, 0, 0, 0.05);
  padding-bottom: 3px;
}
.docsPage .memberLabel {
  cursor: default;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
  font-size: 1em;
  margin: 0;
}
@media only screen and (max-width: 680px) {
  .docsPage .memberLabel {
    cursor: pointer;
  }
}
.docsPage .detail {
  box-sizing: border-box;
  margin-bottom: 2.6rem;
  overflow: hidden;
}
.docsPage .groupTitle {
  color: #9A9C9E;
  font-size: 1.5em;
  font-weight: 300;
  margin: 3rem 0 2rem;
}
@media only screen and (max-width: 680px) {
  .docsPage .groupTitle {
    margin: 2em 0 1em;
  }
}
.docsPage .doc {
  margin: 2em 0 3em;
}
.docsPage p:last-child {
  margin-bottom: 0;
}
