.miniHeader {
  background: #435052;
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
}
.miniHeader .miniLogo {
  width: 169px;
  height: 23px;
  margin-top: 1px;
  background: url(images/fdt_logo_transparent_ondark_23px-7c504a6106dece842f4119f5f8858eb4.png) no-repeat scroll;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .miniHeader .miniLogo {
    background-image: url(images/fdt_logo_transparent_ondark_23px-2x-97023255f18096790984cc0cadbe5f29.png);
    background-size: contain;
  }
}
.miniHeader .lines:after {
  position: absolute;
  content: '';
  border-left: 3px solid #450500;
  height: 12px;
  display: block;
  left: 4px;
  top: -7px;
}
.miniHeader .lines:before {
  position: absolute;
  content: '';
  display: block;
  width: 19px;
  height: 0;
  border-top: 2px solid #ff0;
  top: -7px;
}
.miniHeader .lines {
  display: inline-block;
  position: relative;
  width: 19px;
  top: -1px;
  height: 3px;
  margin-right: 6px;
  border-bottom: 2px solid #fff;
  border-top: 2px solid #fff;
}
.miniHeader .homeLink {
  display: none;
}
@media only screen and (max-width: 680px) {
  .miniHeader .homeLink {
    display: inline;
  }
  .miniHeader .miniLogo {
    display: none;
  }
}
