.homePage .header {
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  user-select: none;
}
.homePage .pageBody {
  background-color: #fff;
  padding: 0 36px;
  position: relative;
  z-index: 1;
}
.homePage .contents {
  margin: 0 auto;
  max-width: 740px;
  padding: 64px 0;
}
.homePage .contents h2 {
  margin: 4rem 0 1rem;
}
.homePage .contents h3 {
  margin: 2rem 0 1rem;
}
.homePage .miniHeader {
  background: #435052;
  position: fixed;
  width: 100%;
  z-index: 2;
}
.homePage .notLoaded {
  visibility: hidden;
}
.homePage .miniHeaderContents {
  margin: 0 auto;
  max-width: 880px;
  padding: 12px 36px;
  text-align: right;
}
@media only screen and (max-width: 680px) {
  .homePage .miniHeaderContents {
    text-align: left;
  }
}
.homePage .miniLogo {
  float: left;
  left: -140px;
  top: 12px;
}
@media only screen and (max-width: 680px) {
  .homePage .miniLogo {
    display: none;
  }
}
.homePage .miniLogo {
  margin: 0;
  line-height: 28px;
  color: #000;
}
.homePage .miniHeaderContents a {
  color: #fff;
  margin-right: 1em;
  text-decoration: none;
  text-shadow: 0 1px rgba(0, 0, 0, 0.15);
}
.homePage .cover .miniHeaderContents a {
  color: #435052;
  text-shadow: 0 1px rgba(255, 255, 255, 0.15);
}
.homePage .miniHeaderContents a:last-child {
  margin-right: 0;
}
.homePage .cover {
  background-color: #bdbdbd;
  border-bottom: 1px solid #ddd;
  height: 70vh;
  max-height: 800px;
  min-height: 260px;
  overflow: hidden;
  position: relative;
  width: 100%;
  z-index: 1;
}
.homePage .filler {
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 1001;
}
.homePage .miniHeader .miniLogo .logoPic {
  width: 169px;
  height: 23px;
  margin-top: 1px;
  background: url(images/fdt_logo_transparent_ondark_23px-7c504a6106dece842f4119f5f8858eb4.png) no-repeat scroll;
}
@media only screen and (-webkit-min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi), only screen and (min-resolution: 2dppx) {
  .homePage .miniHeader .miniLogo .logoPic {
    background-image: url(images/fdt_logo_transparent_ondark_23px-2x-97023255f18096790984cc0cadbe5f29.png);
    background-size: contain;
  }
}
.homePage .logo {
  bottom: 0;
  left: 0;
  right: 0;
  position: fixed;
  top: 25vh;
  text-align: center;
  z-index: 1000;
}
@media only screen and (max-width: 680px) {
  .homePage .logo {
    top: 70px;
  }
}
.homePage .logo .title {
  font-size: 2.9em;
  font-weight: 800;
}
.homePage .logo .subtitle {
  font-size: 1.2em;
  margin-bottom: 18px;
}
.homePage .logo .button {
  background: none;
  border: 2px solid #4183C4;
  border-radius: 4px;
  padding: 8px 16px;
  transition-duration: 0.3s;
  text-transform: uppercase;
  font-weight: 600;
}
.homePage .logo .button:hover {
  background: #4183C4;
  color: white;
}
.homePage .logo .links {
  margin-top: 16px;
}
.homePage .logo .links a {
  margin: 4px;
}
.homePage .heroContainer {
  position: absolute;
}
.homePage .heroContainer:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.8);
  z-index: 1000;
}
@media only screen and (max-width: 680px) {
  .homePage .cover {
    max-height: 260px;
  }
  .homePage .miniHeaderContents {
    padding: 12px;
    text-align: center;
  }
  .homePage .synopsis {
    max-width: 540px;
  }
  .homePage .pageBody {
    padding: 0 12px;
  }
}
